@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
body{
    font-family: 'Architects Daughter', cursive;
}
.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 80vw;
    margin: 5em 10vw;
}


.header{
    display: flex;
    font-size: 24px;
    font-weight: bold;
    justify-content: center;
    align-items: center;
    background-color: red;
}
.card{
    width: 22%;
    height: 400px;
    background: green;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    margin: 8px;
}

.card img{
    height: 80%;
    width: 90%;
}

.title{
    font-size: 24px;
    color: white;
    font-weight: bold;
}
.scores{
    display: flex;
    width: 60%;
    justify-content: space-between;
    align-items: center;
    margin: 0 2em;
}
